.about-container{
    display:grid;
    grid-template-columns: 35% 58%;
    justify-content: space-between;
}

.about-me{
    width:100%;
    aspect-ratio: 1/1;
    border-radius:2rem ;
    background: linear-gradient(45deg,transparent,var(--color-primary),transparent);
    display: grid;
    place-items: center;
}
.about-me img{
    border-radius: 2rem;
    transform: rotate(10deg);
    transition:var(--transition);
}
.about-me img:hover{
    border-radius: 2rem;
    transform: rotate(0);
}
.about-cards{
    display:grid;
    grid-template-columns: repeat(3,1fr);
    gap:1.5rem;
}
.about-card{
    background: var(--color-bg-variant);
    border:1px solid transparent;
    border-radius: 1rem;
    padding:2rem;
    text-align: center;
    transition: var(--transition);
}
.about-card:hover{
    background: transparent;
    border-color:var(--color-primary-variant);
    border-radius: 1rem;
    padding:2rem;
    text-align: center;
    cursor:default;
}
.about-card h5{
    font-size: .95rem;
}
.about-card small{
    font-size: .7rem;
    color:var(--color-light);
}
.about-icon{
    color:var(--color-primary);
    font-size:1.4rem ;
    margin-bottom: 1rem;
}

.about-content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
    text-align: left;
    font-family: "Times New Roman", Times, serif;
 }
.about-content p::first-letter{
   font-size: larger;
   font-weight: bold;
   color:rgb(255,255,255,.7);
 
 }

/* **************MEDIA QUERIES(meduim devices) ****** */
@media screen and (max-width: 1024px) {
    .about-container{
        grid-template-columns:1fr;
        justify-content: center;
    }
    .about-me{
        width:50%;  
        margin:2rem auto 4rem;
    }
    .about-cards{
        
        grid-template-columns: repeat(2,1fr);
        
        gap:1.5rem;
    }
    .about-content p{
        margin:1rem 0 1.5rem;
        
        
     }
    
}
/* **************MEDIA QUERIES(small devices) ****** */
@media screen and (max-width: 600px) {
    .about-me{
        width:65%;
        margin:0 auto 3rem;
    }
    .about-cards{
        grid-template-columns: 1fr 1fr;
        gap:1rem
    }
    .about-content{
        text-align: center;
    }
    .about-content p{

        margin:1.5rem 0;
    }
}

